import proxy from '../proxy'

class Ctrl{
	constructor(app) {
		Object.assign(this, {
			app, 
			model: proxy.classify, 
		})

		this.init()
	}

	/**
	 * 
	 */
	init() {
		this.routes()
	}

	/**
	 * 
	 */
	routes() {
		this.app.get('/api/classify', this.getAll.bind(this))
		this.app.get('/api/classify/:id', this.get.bind(this))
		this.app.post('/api/classify', this.post.bind(this))
		this.app.put('/api/classify/:id', this.put.bind(this))
		this.app.delete('/api/classify/:id', this.delete.bind(this))
	}

	/**
	 * @apiDefine Header
	 * @apiHeader {String} Authorization jsonwebtoken
	 */
	
	/**
	 * @apiDefine Success
	 * @apiSuccess {Object} meta 
	 * @apiSuccess {Number} meta.code 01
	 * @apiSuccess {String} meta.message 
	 * @apiSuccess {Object} data 
	 */
	
	/**
	 * @api {get} /classify 
	 * @apiDescription 
	 * @apiName getAll
	 * @apiGroup classify
	 * 
	 * @apiParam {String} [page=1] 
	 * @apiParam {String} [limit=10] 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /classify
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": [{
	 *       	"_id": "_id",
	 *       	"name": "name",
	 *       	"remark": "remark",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }]
	 *     }
	 */	
	getAll(req, res, next) {
		const query = {}

		const fields = {}

		const options = {
			page : req.query.page, 
			limit: req.query.limit, 
		}

		Promise.all([
			this.model.countAsync(query), 
			this.model.getAll(query, fields, options), 
		])
		.then(docs => {
			res.tools.setJson(0, '', {
				items   : docs[1], 
				paginate: new res.paginate(Number(options.page), Number(options.limit), docs[0]).item, 
			})
		})
		.catch(err => next(err))
	}
	
	/**
	 * @api {get} /classify/:id 
	 * @apiDescription 
	 * @apiName get
	 * @apiGroup classify
	 *
	 * @apiParam {String} id ID
	 *
	 * @apiPermission none
	 * @apiSampleRequest /classify/:id
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id",
	 *       	"name": "name",
	 *       	"remark": "remark",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }
	 *     }
	 */
	get(req, res, next) {
		const query = {
			_id: req.params.id
		}

		const fields = {}

		this.model.get(query, fields)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '', doc)
		})
		.catch(err => next(err))
	}

	/**
	 * @api {post} /classify 
	 * @apiDescription 
	 * @apiName post
	 * @apiGroup classify
	 *
	 * @apiParam {String} name 
	 * @apiParam {String} remark 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /classify
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id"
	 *       }
	 *     }
	 */
	post(req, res, next) {
		const body = {
			name  : req.body.name, 
			remark: req.body.remark, 
		}

		this.model.post(body)
		.then(doc => res.tools.setJson(0, '', {_id: doc._id}))
		.catch(err => next(err))
	}

	/**
	 * @api {put} /classify/:id 
	 * @apiDescription 
	 * @apiName put
	 * @apiGroup classify
	 *
	 * @apiParam {String} id ID
	 * @apiParam {String} [name] 
	 * @apiParam {String} [remark] 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /classify/:id
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id",
	 *       	"name": "name",
	 *       	"remark": "remark",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }
	 *     }
	 */
	put(req, res, next) {
		const query = {
			_id: req.params.id
		}

		const body = {
			name  : req.body.name, 
			remark: req.body.remark, 
		}

		this.model.put(query, body)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '', doc)
		})
		.catch(err => next(err))
	}

	/**
	 * @api {delete} /classify/:id 
	 * @apiDescription 
	 * @apiName delete
	 * @apiGroup classify
	 *
	 * @apiParam {String} id ID
	 * @apiSampleRequest /classify/:id
	 * 
	 * @apiPermission none
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": null
	 *     }
	 */
	delete(req, res, next) {
		const query = {
			_id: req.params.id
		}
		
		this.model.delete(query)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '')
		})
		.catch(err => next(err))
	}
}

export default Ctrl


