import request from 'request'
import config from '../config'
import jwt from '../common/jwtauth'
import proxy from '../proxy'
import jwtauth from '../middlewares/jwtauth'

class Ctrl{
	constructor(app) {
		Object.assign(this, {
			app, 
			model: proxy.user, 
		})

		this.init()
	}

	/**
	 * 
	 */
	init() {
		this.routes()
		this.initSuperAdmin()
	}

	/**
	 * 
	 */
	routes() {
		this.app.post('/api/user/wechat/sign/up', this.wechatSignUp.bind(this))
		this.app.post('/api/user/wechat/sign/in', this.wechatSignIn.bind(this))
		this.app.post('/api/user/sign/up', this.signUp.bind(this))
		this.app.post('/api/user/sign/in', this.signIn.bind(this))
		this.app.post('/api/user/sign/out', this.signOut.bind(this))
		this.app.post('/api/user/reset/password', this.resetPassword.bind(this))
		this.app.post('/api/user/info', this.saveInfo.bind(this))
		this.app.get('/api/user/info', this.getInfo.bind(this))
	}

	/**
	 * request
	 */
	requestAsync(url) {
		return new Promise((reslove, reject) => {
			request({url: url}, (err, res, body) => {
				if (err) return reject(err)
				return reslove(body)
			})
		})
	}

	/**
	 * code  session_key
	 */
	getSessionKey(code) {
		const appid = config.wechat.appid
		const secret = config.wechat.secret
		const url = `https://api.weixin.qq.com/sns/jscode2session?appid=${appid}&secret=${secret}&js_code=${code}&grant_type=authorization_code`
		return this.requestAsync(url)
	}

	/**
	 * @api {post} /user/wechat/sign/up 
	 * @apiDescription 
	 * @apiName wechatSignUp
	 * @apiGroup user
	 *
	 * @apiParam {String} code 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /user/wechat/sign/up
	 * 
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"token": "token"
	 *       }
	 *     }
	 */
	wechatSignUp(req, res, next) {
		const code = req.body.code
		const body = {
			username: null, 
			password: res.jwt.setMd5('123456'), 
		}

		this.getSessionKey(code)
		.then(doc => {
			doc = JSON.parse(doc)
			if (doc && doc.errmsg) return res.tools.setJson(doc.errcode, doc.errmsg)
			if (doc && doc.openid) {
				body.username = doc.openid
				return this.model.findByName(doc.openid)
			}
		})
		.then(doc => {
			if (!doc) return this.model.newAndSave(body)
			if (doc && doc._id) return res.tools.setJson(1, '')
		})
		.then(doc => {
			if (doc && doc._id) return res.tools.setJson(0, '', {
				token: res.jwt.setToken(doc._id)
			})
		})
		.catch(err => next(err))
	}

	/**
	 * @api {post} /user/wechat/sign/in 
	 * @apiDescription 
	 * @apiName wechatSignIn
	 * @apiGroup user
	 *
	 * @apiParam {String} code 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /user/wechat/sign/in
	 * 
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"token": "token"
	 *       }
	 *     }
	 */
	wechatSignIn(req, res, next) {
		const code = req.body.code

		this.getSessionKey(code)
		.then(doc => {
			doc = JSON.parse(doc)
			if (doc && doc.errmsg) return res.tools.setJson(doc.errcode, doc.errmsg)
			if (doc && doc.openid) return this.model.findByName(doc.openid)
		})
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			if (doc && doc._id) return res.tools.setJson(0, '', {
				token: res.jwt.setToken(doc._id)
			})
		})
		.catch(err => next(err))
	}

	/**
	 * 
	 */
	initSuperAdmin(req, res, next) {
		const username = config.superAdmin.username
		const password = config.superAdmin.password

		this.model.findByName(username)
		.then(doc => {
			if (!doc) return this.model.newAndSave({
				username: username, 
				password: jwt.setMd5(password), 
			})
		})
	}

	/**
	 * @api {post} /user/sign/up 
	 * @apiDescription 
	 * @apiName signUp
	 * @apiGroup user
	 *
	 * @apiParam {String} username 
	 * @apiParam {String} password 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /user/sign/up
	 * 
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": null
	 *     }
	 */
	signUp(req, res, next) {
		const username = req.body.username
		const password = req.body.password

		if (!username || !password) return res.tools.setJson(1, '')
		
		this.model.findByName(username)
		.then(doc => {
			if (!doc) return this.model.newAndSave({
				username: username, 
				password: res.jwt.setMd5(password)
			})
			return res.tools.setJson(1, '')
		})
		.then(doc => res.tools.setJson(0, ''))
		.catch(err => next(err))
	}

	/**
	 * @api {post} /user/sign/in 
	 * @apiDescription 
	 * @apiName signIn
	 * @apiGroup user
	 *
	 * @apiParam {String} username 
	 * @apiParam {String} password 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /user/sign/in
	 * 
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"token": "token"
	 *       }
	 *     }
	 */
	signIn(req, res, next) {
		const username = req.body.username
		const password = req.body.password
		
		if (!username || !password) return res.tools.setJson(1, '')	
		if (req.body.code !== req.session.code) return res.tools.setJson(1, '')

		this.model.model.getAuthenticated(username, password)
		.then(doc => {
			switch (doc) {
	            case 0:
	            	res.tools.setJson(1, '')
	            	break
	            case 1:
	                res.tools.setJson(1, '')
	                break
	            case 2:
	                res.tools.setJson(1, '')
	                break
	            default: res.tools.setJson(0, '', {
					token: res.jwt.setToken(doc._id)
				})
	        }
		})
		.catch(err => next(err))	
	}

	/**
	 * @api {post} /user/sign/out 
	 * @apiDescription 
	 * @apiName signOut
	 * @apiGroup user
	 *
	 * @apiPermission none
	 * @apiSampleRequest /user/sign/out
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": null
	 *     }
	 */
	signOut(req, res, next) {
		if (req.user) {
			new jwtauth().expireToken(req.headers)
			delete req.user	
			delete this.app.locals.token
			return res.tools.setJson(0, '')
		}
		return res.tools.setJson(1, '')
	}

	/**
	 * @api {post} /user/reset/password 
	 * @apiDescription 
	 * @apiName resetPassword
	 * @apiGroup user
	 *
	 * @apiParam {String} oldpwd 
	 * @apiParam {String} newpwd 
	 * 
	 * @apiPermission none
	 * @apiSampleRequest /user/reset/password
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": null
	 *     }
	 */
	resetPassword(req, res, next) {
		const oldpwd = req.body.oldpwd
		const newpwd = req.body.newpwd
			
		if (oldpwd && newpwd) {
			this.model.findByName(req.user.username)
			.then(doc => {
				if (!doc) return res.tools.setJson(1, '')
				if (doc.password !== res.jwt.setMd5(oldpwd)) return res.tools.setJson(1, '')
				doc.password = res.jwt.setMd5(newpwd)
				return doc.save()
			})
			.then(doc => res.tools.setJson(0, ''))
			.catch(err => next(err))
		}
	}

	/**
	 * @api {post} /user/info 
	 * @apiDescription 
	 * @apiName saveInfo
	 * @apiGroup user
	 *
	 * @apiParam {Date} birthday 
	 * @apiParam {String} email 
	 * @apiParam {String} gender 
	 * @apiParam {String} avatar 
	 * @apiParam {String} nickname 
	 * @apiParam {String} tel 
	 * 
	 * @apiPermission none
	 * @apiSampleRequest /user/info
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {}
	 *     }
	 */
	saveInfo(req, res, next) {
		this.model.findByName(req.user.username)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')

			for(let key in req.body) {
				doc[key] = req.body[key]
			}

			doc.update_at = Date.now()

			return doc.save()
		})
		.then(doc => res.tools.setJson(0, '', doc))
		.catch(err => next(err))
	}

	/**
	 * @api {get} /user/info 
	 * @apiDescription 
	 * @apiName getInfo
	 * @apiGroup user
	 * 
	 * @apiPermission none
	 * @apiSampleRequest /user/info
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {}
	 *     }
	 */
	getInfo(req, res, next) {
		this.model.findByName(req.user.username)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '', doc)
		})
		.catch(err => next(err))
	}
}

export default Ctrl


