import Model from '../models/user'

class Common{
	constructor(model) {
		Object.assign(this, {
			model, 
		})
	}

	/**
	 * 
	 * @param  {Object} body 
	 * @return {Function}          
	 */
	newAndSave(body) {
		return new this.model(body).save()
	}

	/**
	 * 
	 * @param  {String}   username 
	 * @return {Function}            
	 */
	findByName(username) {
		return this.model.findOneAsync({
			username: username
		})
	}
}

export default new Common(Model)


