import proxy from '../proxy'

class Ctrl{
	constructor(app) {
		Object.assign(this, {
			app, 
			model: proxy.order, 
		})

		this.init()
	}

	/**
	 * 
	 */
	init() {
		this.routes()
	}

	/**
	 * 
	 */
	routes() {
		this.app.get('/api/order', this.getAll.bind(this))
		this.app.get('/api/order/:id', this.get.bind(this))
		this.app.post('/api/order', this.post.bind(this))
		this.app.put('/api/order/:id', this.put.bind(this))
		this.app.delete('/api/order/:id', this.delete.bind(this))
	}

	/**
	 * @apiDefine Header
	 * @apiHeader {String} Authorization jsonwebtoken
	 */
	
	/**
	 * @apiDefine Success
	 * @apiSuccess {Object} meta 
	 * @apiSuccess {Number} meta.code 01
	 * @apiSuccess {String} meta.message 
	 * @apiSuccess {Object} data 
	 */
	
	/**
	 * @api {get} /order 
	 * @apiDescription 
	 * @apiName getAll
	 * @apiGroup order
	 * 
	 * @apiParam {String} [page=1] 
	 * @apiParam {String} [limit=10] 
	 * @apiParam {Boolean} [is_show] is_show
	 *
	 * @apiPermission none
	 * @apiSampleRequest /order
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": [{
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }]
	 *     }
	 */
	getAll(req, res, next) {
		const status = req.query.type

		const query = {
			user  : req.user._id,
			status: status,
		}

		status === 'all' && delete query.status

		const opts = {
			page : req.query.page, 
			limit: req.query.limit, 
		}

		const params = {
			query  : query, 
			fields : {}, 
			options: opts, 
		}

		const options = {
			path    : 'user', 
			select  : {}, 
		}

		Promise.all([
			this.model.countAsync(query), 
			this.model.findAndPopulateAsync(params, options), 
		])
		.then(docs => {
			res.tools.setJson(0, '', {
				items   : docs[1], 
				paginate: new res.paginate(Number(opts.page), Number(opts.limit), docs[0]).item, 
			})
		})
		.catch(err => next(err))
	}
	
	/**
	 * @api {get} /order/:id 
	 * @apiDescription 
	 * @apiName get
	 * @apiGroup order
	 *
	 * @apiParam {String} id ID
	 *
	 * @apiPermission none
	 * @apiSampleRequest /order/:id
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }
	 *     }
	 */
	get(req, res, next) {
		const query = {
			_id : req.params.id, 
			user: req.user._id, 
		}

		const params = {
			query  : query, 
			fields : {}, 
			options: {}, 
		}

		const options = {
			path    : 'user', 
			select  : {}, 
		}

		this.model.findOneAndPopulateAsync(params, options)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '', doc)
		})
		.catch(err => next(err))
	}

	/**
	 * @api {post} /order 
	 * @apiDescription 
	 * @apiName post
	 * @apiGroup order
	 *
	 * @apiParam {String} title 
	 * @apiParam {String} remark 
	 * @apiParam {Number} sort 
	 * @apiParam {Boolean} is_show 
	 * @apiParam {Array} images 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /order
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id"
	 *       }
	 *     }
	 */
	post(req, res, next) {
		const body = {
			items      : [], 
			totalAmount: 0, 
			address_id : req.body.address_id, 
			user       : req.user._id, 
		}

		const query = {
			_id: {
				$in: req.body.items.map(n => n.id),
			},
		}

		const params = {
			query  : query, 
			fields : {}, 
			options: {}, 
		}

		const options = {
			path    : 'types', 
			select  : {}, 
		}

		proxy.address.findByIdAsync(body.address_id)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			body.recipientName = doc.name
			body.recipientGender = doc.gender
			body.recipientTel = doc.tel
			body.recipientAddress = doc.address
			return proxy.goods.findAndPopulateAsync(params, options)
		})
		.then(doc => {
			doc.forEach(n => {
				const items = {
					goods: n,
					meta : {},
				}
				req.body.items.forEach(m => {
					if (n._id.toString() === m.id.toString()) {
						items.meta.total = Math.abs(m.total)
						items.meta.totalAmount = Math.abs(n.price * m.total)
						body.totalAmount += items.meta.totalAmount
					}
				})
				body.items.push(items)
			})
			return this.model.post(body)
		})
		.then(doc => {
			proxy.cart.removeAsync({
				user: req.user._id,
				goods: {
					$in: req.body.items.map(n => n.id),
				},
			})
			res.tools.setJson(0, '', {_id: doc._id})
		})
		.catch(err => next(err))
	}

	/**
	 * @api {put} /order/:id 
	 * @apiDescription 
	 * @apiName put
	 * @apiGroup order
	 *
	 * @apiParam {String} id ID
	 * @apiParam {String} [title] 
	 * @apiParam {String} [remark] 
	 * @apiParam {Number} [sort] 
	 * @apiParam {Boolean} [is_show] 
	 * @apiParam {Array} [images] 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /order/:id
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }
	 *     }
	 */
	put(req, res, next) {
		const query = {
			_id : req.params.id, 
			user: req.user._id, 
		}

		const body = {
			title    : req.body.title,
			remark   : req.body.remark,
			sort     : req.body.sort || 99,
			is_show  : req.body.is_show,
			images   : req.body.images,
		}

		this.model.put(query, body)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '', doc)
		})
		.catch(err => next(err))
	}

	/**
	 * @api {delete} /order/:id 
	 * @apiDescription 
	 * @apiName delete
	 * @apiGroup order
	 *
	 * @apiParam {String} id ID
	 * @apiSampleRequest /order/:id
	 * 
	 * @apiPermission none
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": null
	 *     }
	 */
	delete(req, res, next) {
		const query = {
			_id : req.params.id, 
			user: req.user._id, 
		}
		
		this.model.delete(query)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '')
		})
		.catch(err => next(err))
	}
}

export default Ctrl


