import proxy from '../proxy'

class Ctrl{
	constructor(app) {
		Object.assign(this, {
			app, 
			model: proxy.banner, 
		})

		this.init()
	}

	/**
	 * 
	 */
	init() {
		this.routes()
	}

	/**
	 * 
	 */
	routes() {
		this.app.get('/api/banner', this.getAll.bind(this))
		this.app.get('/api/banner/:id', this.get.bind(this))
		this.app.post('/api/banner', this.post.bind(this))
		this.app.put('/api/banner/:id', this.put.bind(this))
		this.app.delete('/api/banner/:id', this.delete.bind(this))
	}

	/**
	 * @apiDefine Header
	 * @apiHeader {String} Authorization jsonwebtoken
	 */
	
	/**
	 * @apiDefine Success
	 * @apiSuccess {Object} meta 
	 * @apiSuccess {Number} meta.code 01
	 * @apiSuccess {String} meta.message 
	 * @apiSuccess {Object} data 
	 */
	
	/**
	 * @api {get} /banner 
	 * @apiDescription 
	 * @apiName getAll
	 * @apiGroup banner
	 * 
	 * @apiParam {String} [page=1] 
	 * @apiParam {String} [limit=10] 
	 * @apiParam {Boolean} [is_show] is_show
	 *
	 * @apiPermission none
	 * @apiSampleRequest /banner
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": [{
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }]
	 *     }
	 */
	getAll(req, res, next) {
		const query = {}

		const fields = {}

		const options = {
			page : req.query.page, 
			limit: req.query.limit, 
		}

		if (req.query.is_show) {
			query.is_show = true
		}

		Promise.all([
			this.model.countAsync(query), 
			this.model.getAll(query, fields, options), 
		])
		.then(docs => {
			res.tools.setJson(0, '', {
				items   : docs[1], 
				paginate: new res.paginate(Number(options.page), Number(options.limit), docs[0]).item, 
			})
		})
		.catch(err => next(err))
	}
	
	/**
	 * @api {get} /banner/:id 
	 * @apiDescription 
	 * @apiName get
	 * @apiGroup banner
	 *
	 * @apiParam {String} id ID
	 *
	 * @apiPermission none
	 * @apiSampleRequest /banner/:id
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }
	 *     }
	 */
	get(req, res, next) {
		const query = {
			_id: req.params.id
		}

		const fields = {}

		this.model.get(query, fields)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '', doc)
		})
		.catch(err => next(err))
	}

	/**
	 * @api {post} /banner 
	 * @apiDescription 
	 * @apiName post
	 * @apiGroup banner
	 *
	 * @apiParam {String} title 
	 * @apiParam {String} remark 
	 * @apiParam {Number} sort 
	 * @apiParam {Boolean} is_show 
	 * @apiParam {Array} images 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /banner
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id"
	 *       }
	 *     }
	 */
	post(req, res, next) {
		const body = {
			title    : req.body.title,
			remark   : req.body.remark,
			sort     : req.body.sort || 99,
			is_show  : req.body.is_show,
			images   : req.body.images,
		}

		this.model.post(body)
		.then(doc => res.tools.setJson(0, '', {_id: doc._id}))
		.catch(err => next(err))
	}

	/**
	 * @api {put} /banner/:id 
	 * @apiDescription 
	 * @apiName put
	 * @apiGroup banner
	 *
	 * @apiParam {String} id ID
	 * @apiParam {String} [title] 
	 * @apiParam {String} [remark] 
	 * @apiParam {Number} [sort] 
	 * @apiParam {Boolean} [is_show] 
	 * @apiParam {Array} [images] 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /banner/:id
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }
	 *     }
	 */
	put(req, res, next) {
		const query = {
			_id: req.params.id
		}

		const body = {
			title    : req.body.title,
			remark   : req.body.remark,
			sort     : req.body.sort || 99,
			is_show  : req.body.is_show,
			images   : req.body.images,
		}

		this.model.put(query, body)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '', doc)
		})
		.catch(err => next(err))
	}

	/**
	 * @api {delete} /banner/:id 
	 * @apiDescription 
	 * @apiName delete
	 * @apiGroup banner
	 *
	 * @apiParam {String} id ID
	 * @apiSampleRequest /banner/:id
	 * 
	 * @apiPermission none
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": null
	 *     }
	 */
	delete(req, res, next) {
		const query = {
			_id: req.params.id
		}
		
		this.model.delete(query)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '')
		})
		.catch(err => next(err))
	}
}

export default Ctrl


