import proxy from '../proxy'

class Ctrl{
	constructor(app) {
		Object.assign(this, {
			app, 
			model: proxy.cart, 
		})

		this.init()
	}

	/**
	 * 
	 */
	init() {
		this.routes()
	}

	/**
	 * 
	 */
	routes() {
		this.app.get('/api/cart', this.getAll.bind(this))
		this.app.get('/api/cart/:id', this.get.bind(this))
		this.app.post('/api/cart', this.post.bind(this))
		this.app.put('/api/cart/:id', this.put.bind(this))
		this.app.delete('/api/cart/:id', this.delete.bind(this))
		this.app.post('/api/cart/clear', this.clear.bind(this))
	}

	/**
	 * @apiDefine Header
	 * @apiHeader {String} Authorization jsonwebtoken
	 */
	
	/**
	 * @apiDefine Success
	 * @apiSuccess {Object} meta 
	 * @apiSuccess {Number} meta.code 01
	 * @apiSuccess {String} meta.message 
	 * @apiSuccess {Object} data 
	 */
	
	/**
	 * @api {get} /cart 
	 * @apiDescription 
	 * @apiName getAll
	 * @apiGroup cart
	 * 
	 * @apiParam {String} [page=1] 
	 * @apiParam {String} [limit=10] 
	 * @apiParam {Boolean} [is_show] is_show
	 *
	 * @apiPermission none
	 * @apiSampleRequest /cart
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": [{
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }]
	 *     }
	 */
	getAll(req, res, next) {
		const query = {
			user: req.user._id
		}

		const params = {
			query  : query, 
			fields : {}, 
			options: {}, 
		}

		const options = {
			path    : 'goods', 
			select  : {}, 
		}

		this.model.findAndPopulateAsync(params, options)
		.then(docs => res.tools.setJson(0, '', docs))
		.catch(err => next(err))
	}
	
	/**
	 * @api {get} /cart/:id 
	 * @apiDescription 
	 * @apiName get
	 * @apiGroup cart
	 *
	 * @apiParam {String} id ID
	 *
	 * @apiPermission none
	 * @apiSampleRequest /cart/:id
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }
	 *     }
	 */
	get(req, res, next) {
		const query = {
			_id : req.params.id, 
			user: req.user._id, 
		}

		const params = {
			query  : query, 
			fields : {}, 
			options: {}, 
		}

		const options = {
			path    : 'goods', 
			select  : {}, 
		}

		this.model.findOneAndPopulateAsync(params, options)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '', doc)
		})
		.catch(err => next(err))
	}

	/**
	 * @api {post} /cart 
	 * @apiDescription 
	 * @apiName post
	 * @apiGroup cart
	 *
	 * @apiParam {String} title 
	 * @apiParam {String} remark 
	 * @apiParam {Number} sort 
	 * @apiParam {Boolean} is_show 
	 * @apiParam {Array} images 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /cart
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id"
	 *       }
	 *     }
	 */
	post(req, res, next) {
		const query = {
			goods : req.body.goods, 
			user : req.user._id, 
		}

		const body = {
			goods : req.body.goods, 
			total: Number(req.body.total) || 1, 
			user : req.user._id, 
		}

		const p1 = proxy.goods.findByIdAsync(query.goods)
		const p2 = this.model.findOneAsync(query)

		Promise.all([p1, p2])
		.then(doc => {
			const goods = doc[0]
			const cart  = doc[1]

			if (!goods) return res.tools.setJson(1, '')

			if (!cart) {
				body.amount = goods.price
				body.totalAmount = goods.price * body.total
				return this.model.post(body)
			}

			cart.total = cart.total + body.total
			cart.amount = goods.price
			cart.totalAmount = goods.price * cart.total
			return cart.save()
		})
		.then(doc => res.tools.setJson(0, '', {_id: doc._id}))
		.catch(err => next(err))
	}

	/**
	 * @api {put} /cart/:id 
	 * @apiDescription 
	 * @apiName put
	 * @apiGroup cart
	 *
	 * @apiParam {String} id ID
	 * @apiParam {String} [title] 
	 * @apiParam {String} [remark] 
	 * @apiParam {Number} [sort] 
	 * @apiParam {Boolean} [is_show] 
	 * @apiParam {Array} [images] 
	 *
	 * @apiPermission none
	 * @apiSampleRequest /cart/:id
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": {
	 *       	"_id": "_id",
	 *       	"images": [{
	 *       		"_id": "_id",
	 *       		"name": "name",
	 *       		"path": "path",
	 *       		"create_at": "create_at"
	 *       	}],
	 *       	"is_show": "is_show",
	 *       	"remark": "remark",
	 *       	"sort": "sort",
	 *       	"title": "title",
	 *       	"create_at": "create_at",
	 *       	"update_at": "update_at"
	 *       }
	 *     }
	 */
	put(req, res, next) {
		const query = {
			_id : req.params.id, 
			user: req.user._id, 
		}

		const body = {
			total: req.body.total, 
		}

		this.model.findOneAsync(query)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			doc.total = Math.abs(body.total)
			doc.totalAmount = Math.abs(doc.amount * doc.total)
			return doc.save()
		})
		.then(doc => res.tools.setJson(0, '', doc))
		.catch(err => next(err))
	}

	/**
	 * @api {delete} /cart/:id 
	 * @apiDescription 
	 * @apiName delete
	 * @apiGroup cart
	 *
	 * @apiParam {String} id ID
	 * @apiSampleRequest /cart/:id
	 * 
	 * @apiPermission none
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": null
	 *     }
	 */
	delete(req, res, next) {
		const query = {
			_id : req.params.id, 
			user: req.user._id, 
		}
		
		this.model.delete(query)
		.then(doc => {
			if (!doc) return res.tools.setJson(1, '')
			return res.tools.setJson(0, '')
		})
		.catch(err => next(err))
	}

	/**
	 * @api {clear} /cart/clear 
	 * @apiDescription 
	 * @apiName clear
	 * @apiGroup cart
	 *
	 * @apiSampleRequest /cart/clear
	 * 
	 * @apiPermission none
	 * 
	 * @apiUse Header
	 * @apiUse Success
	 *
	 * @apiSuccessExample Success-Response:
	 *     HTTP/1.1 200 OK
	 *     {
	 *       "meta": {
	 *       	"code": 0,
	 *       	"message": ""
	 *       },
	 *       "data": null
	 *     }
	 */
	clear(req, res, next) {
		const query = {
			user: req.user._id
		}

		this.model.removeAsync(query)
		.then(doc => res.tools.setJson(0, ''))
		.catch(err => next(err))
	}
}

export default Ctrl


